export interface Validator {
  name: string;
  validator: any;
  message: string;
}

export interface IDynamicBaseControl {
  labelCode: string,
  name: string,
  control: {
    controlType: string,
    readonly: boolean
  },
  value?: any,
  className?: string,
  validations?: Validator[],
  groupName: string
}

export interface IDynamicDateControl extends IDynamicBaseControl{
  control: {
    controlType: string,
    format?: string,
    readonly: boolean
  }
}

export interface IDynamicDropdownControl extends IDynamicBaseControl{
  control: {
    controlType: string,
    idField: string,
    textField: string,
    multiSelect?: boolean,
    data?: Array<any>
    uri?: string,
    readonly: boolean
  }
}

export interface IDyanmicNumberControl extends IDynamicBaseControl{
  control: {
    controlType: string,
    precision?: number,
    max?: number,
    min?: number,
    readonly: boolean
  }
}
